import Vue from 'vue';
import api from './modules';
import './api.cofing';

Vue.api = {};

const install = (Vue) => {
  if (install.installed) {
    return;
  }
  Vue.api['core'] = api; // 配置核心模块的api

  Object.defineProperties(Vue.prototype, {
    $api: {
      get () {
        return Vue.api;
      }
    }
  });
};

if (typeof window !== 'undefined' && window.Vue) {
  window.Vue.use(install);
}

export default {
  install
};



