  @import "../../../assets/scss/mixin";

  .content-box-card{
    border: 1px solid $border-color;
    padding: 15px;
    @include clearfix;
    .el-form-item {
      /*.el-input{*/
        /*width: 100% !important;*/
      /*}*/
      .el-date-editor {
        width: 100% !important;
      }
      .el-select {
        width: 100% !important;
      }
      .el-input-number{
        width: 100% !important;
      }
      .el-cascader {
